@import "core/variables";
@import "core/reset";
@import "components/code";
@import "components/links";
@import "layout/page";
@import "layout/header";
@import "layout/footer";
@import "pages/index";

/* COLORS
Verde oscuro: #099d8d
Verde claro: #14d9c4
Blanco: #ffffff
Negro: #000000
Gris oscuro: #54585a
Gris claro: #b8b8b9
Gris de fondo: #f1f1f1
*/

:root {
  --brand-color: #099d8d;
  --brand-font: "Open Sans", sans-serif;
  --brand-font2: "Rubik", sans-serif;
  --paragraph-color: #54585a;
}

body {
  background-color: white;
}

.container-margin {
  margin: 0 15px;
}

.header {
  z-index: 2; /*esto es para que la imagen esté por detrás de nav y nombre del euipo*/
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  position: fixed;
}

.name-team {
  width: 100%;
  height: 70px;
  /*background-color: var(--brand-color);*/
  background-color: hsla(174, 83%, 46%, 0.9);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.name {
  font-family: var(--brand-font2);
  color: white;
  text-transform: uppercase;
}

.menu-top {
  background-color: rgba(20, 217, 196, 0.6);
  width: 100%;
  height: 40px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.list {
  list-style-type: none;
  display: inline-block;
  font-family: var(--brand-font2);
  font-size: 15px;
  text-transform: uppercase;
  color: white;
  box-sizing: border-box;
  text-decoration: none;
}

/*flexbox*/
.equipo-1 {
  margin: 8px 15px 7px 16px;
}

.who-we-are {
  margin: 8px 15px 7px 15px;
}

.contacto {
  margin: 8px 16px 7px 15px;
}

.link,
.link:hover,
.link:focus,
.link:active {
  text-decoration: none;
  color: white;
}

/*.link:visited y ponerle el color que queramos si se decide*/

.foto {
  width: 100%;
  height: 421px;
  background-image: url(../images/proyecto.jpg);
  background-size: cover;
  position: relative;
  bottom: 30px;

  z-index: 1;
}
.section-lema {
  height: 425px;
}
.lema {
  background-color: white;
  font-family: var(--brand-font);
  font-size: 20px;
  text-align: left;
  margin-left: 15px;
  margin-right: 15px;
  padding: 15px;
  line-height: 1.2;
  position: relative;
  top: 390px;
  z-index: 2;
}

/* Css Jenny*/
.caja {
  box-sizing: border-box;
  width: 100%;
  height: 399px;
  padding: 30px 30px;
}
.equipo {
  font-family: var(--brand-font2);
  font-size: 24px;
  font-weight: 500;
  line-height: 1.88;
  text-align: center;
  color: var(--brand-color);
}
.h2-equipo {
  text-transform: uppercase;
}
.box {
  margin: 15px 0 0;
  opacity: 0.67;
  font-family: var(--brand-font);
  font-size: 16px;

  line-height: 1.5;
  letter-spacing: normal;
  text-align: center;
  color: var(--paragraph-color);
}

/* añadido Daniela Salazar */
.caracter {
  box-sizing: border-box;
  background-color: rgba(184, 184, 185, 0.2);
  width: 100%;
  line-height: 1.5;
  text-align: center;
  padding: 0 15px;
  padding-bottom: 30px;
}

.h3 {
  color: var(--brand-color);
  font-family: var(--brand-font2);
  font-size: 22px;
  margin-top: 30px;
  padding-bottom: 10px;
  text-transform: capitalize;
  font-weight: bold;
}

.rectangulo-pequeño {
  background-color: white;
  width: 30px;
  height: 30px;
  margin: auto;
  transform: translate(0, -50%) rotate(45deg);
}

/* ___________________ */
/* QUIENES SOMOS*/

.section-who-we-are {
  margin: 30px 15px 45px 15px;
}

.section-who-we-are h2 {
  color: var(--brand-color);
  font-family: var(--brand-font2);
  font-size: 24px;
  line-height: 45px;
  text-transform: uppercase;
  text-align: center;
}

.team-member {
  margin-bottom: 60px;
}

.photo-person {
  display: block;
  margin: 30px auto 0 auto;
  height: 190px;
  width: 190px;
  border-radius: 10px;
  border: solid 5px rgba(0, 0, 0, 0.08);
}

.person {
  color: black;
  font-size: 20px;
  font-family: var(--brand-font);
  line-height: 45px;
  text-align: center;
  margin-top: 15px;
}

.description {
  color: #54585a;
  font-size: 16px;
  line-height: 24px;
  font-family: var(--brand-font);
  text-align: center;
  margin: 0px 10px 15px 10px;
}

.social-media {
  width: 124px;
  margin: 0 auto;
  display: flex;
  justify-content: space-evenly;
}
.media {
  color: var(--brand-color);
  font-size: 16px;
  line-height: 30px;
}

/* Parte Irene */
.footer {
  display: flex;
  height: 250px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-top: solid 1px #b8b8b9;
}
.copy {
  padding-top: 30px;
  color: #b8b8b9;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
}
.link-bottom {
  text-decoration: none;
}

.link-bottom,
.link-bottom:hover,
.link-bottom:focus,
.link-bottom:active {
  text-decoration: none;
  color: #54585a;
}
/* .link-bottom:visited {
    color: #5aabd3;
  } */
.menu-bottom {
  font-size: 14px;
  color: #54585a;
  padding-bottom: 30px;
  padding-top: 20px;
  font-family: var(--brand-font);
  font-weight: 600;
  font-size: 14px;
  line-height: 1.3;
  text-align: center;
}
.logo {
  width: 115.9px;
  height: 45px;
  display: block;
  margin: auto;
}

/* TABLET 768px*/
@media all and (min-width: 768px) {
  /* cabecera */
  .header {
    flex-wrap: nowrap;
    justify-content: space-between;
  }
  .head-tablet {
    width: 100%;
    height: 60px;
    background-color: hsla(174, 83%, 46%, 0.9);
  }

  /* lema */
  .lema {
    width: 336px;
    height: 80px;
    margin-left: 46px;
    padding: 15px 80px 15px 30px;
    top: 340px;
    color: white;
    background-color: rgba(0, 0, 0, 0.4);
  }
}
